import {post} from '../../../common/utils/http';
import * as t from './actionTypes';

export function requestList(params) {
    return (dispatch, getState) => {
        dispatch({
            type: t.GET_LIST
        });
        const url = 'siyuan/SyCtrl.php';
        return post(url, params).then((data) => {
            dispatch({
                type: t.GET_LIST_SUCCESS,
                data
            });
            return data;
        }).catch((error) => {
            dispatch({
                type: t.GET_LIST_FAIL
            });
            return error;
        });
    }
}

export const requestGoods = () => {
    return fetch('/v1/goods', {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Accept': 'application/json, text/plain, */*',
        }
    });
}


