import * as React from 'react';
import Header from './components/Header';
import List from './components/List';
import Card from './components/Card';
import Select from '../../common/components/Select';
import Button from '../../common/components/Button';
import styles from './index.scss';

const TEXT_NO_RECORD = '';
const BTN_TO_PAY = '';


class PaymentRecord extends React.Component {

    static defaultProps = {
        dataList: [
            {
                year: '2018',
                data: [
                    {
                        month: 1,
                        address: '',
                        fee: '1,123.00',
                        isPaid: true
                    },
                    {
                        month: 2,
                        address: '',
                        fee: '1,123.00',
                        isPaid: false
                    },
                    {
                        month: 3,
                        address: '',
                        fee: '1,123.00',
                        isPaid: false
                    },
                    {
                        month: 4,
                        address: '',
                        fee: '1,123.00',
                        isPaid: false
                    }
                ]
            },
            {
                year: '2017',
                data: []
            }
        ]
    }

    constructor(props) {
        super(props);
        this.state = {
            isList: true
        };
    }

    onChangeView = (isList) => {
        this.setState({
            isList
        });
    }

    onToPay = () => {
        const {history} = this.props;
        history.push('/fee');
    }

    renderListView = (dataList) => {
        const { history } = this.props;
        if (Array.isArray(dataList)) {
            return dataList.map((item, index) => {
                return <List history={history}
                    data={item}
                    key={`List-${item.year}-${index}`} />
            });
        }

        return null;
    }

    renderCardView = (dataList) => {
        const { history } = this.props;
        if (Array.isArray(dataList)) {
            return dataList.map((item, index) => {
                return <Card history={history}
                    data={item}
                    key={`Card-${item.year}-${index}`} />
            })
        }
    }

    renderDefault = () => {
        return (
            <div className={styles.default}>
                <div className={styles.bg}>
                </div>
                <span className={styles.noRecord}>
                    {TEXT_NO_RECORD}
                </span>
                <Button rootClass={styles.btnPay}
                onClick={this.onToPay} 
                label={BTN_TO_PAY}></Button>
            </div>
        )
    }

    render() {
        const { isList } = this.state;
        const { dataList } = this.props;
        if ((!dataList || !dataList.length)) {
            return (
                <div className={styles.root}>
                    <Header isHideSwitch={true} />
                    {
                        this.renderDefault()
                    }
                </div>
            )
        }
        return (
            <div className={styles.root}>
                <Header onChangeView={this.onChangeView} />
                <div className={styles.selectContainer}>
                    <Select rootClass={styles.select} />
                </div>
                {
                        isList ? this.renderListView(dataList) : this.renderCardView(dataList)
                }
            </div>
        )
    }
}

export default PaymentRecord;


