/**
 * 
 */ 
import * as React from 'react';

import IconInput from '../../common/components/IconInput';
import Radio from '../../common/components/Radio';
import Button from '../../common/components/Button';

import styles from './index.scss';

const PORTRAIT_ICON = require('../../common/resources/me.png');
const PHONE_ICON = require('../../common/resources/phone.png');
const KEY_ICON = require('../../common/resources/key.png');
const RADIO_DATA = {
    key: 'bdfy',
    label: ''
};
const SIGNUP = '';
const PREFIX_PROTOCOL = '';
const PROTOCOL = '';

class Signup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div className={styles.root}>
                <div className={styles.logo}>
                </div>
                <div className={styles.form}>
                    <IconInput icon={PORTRAIT_ICON} />
                    <IconInput icon={PHONE_ICON} />
                    <IconInput icon={KEY_ICON} hasRightButton={true} />
                    <Radio data={RADIO_DATA} />
                    <Button rootClass={styles.login} label={SIGNUP} />
                    <div className={styles.container}>
                        {PREFIX_PROTOCOL}
                        <a className={styles.protocol}
                            href={"https://www.baidu.com"}>
                            {PROTOCOL}
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}

export default Signup;


