import * as React from 'react';
import { withRouter } from 'react-router';
import Collapse from '../../../../common/components/Collapse';
import Button from '../../../../common/components/Button';
import styles from './index.scss';


const TEXT_LEFT = '';
const ICON_ROOM = require('../../../../common/resources/room.png');
const BTN_TO_PAY = '';

class FeeRecord extends React.Component {

    static defaultProps = {
        data: [],
        address: '',
        name: ''
    };

    onToPay = () => {
        const { history } = this.props;
        history.push('/fee')
    }

    renderHeader = () => {
        return (
            <span className={styles.header}>
                <span className={styles.symbol}>
                </span>
                <span className={styles.label}>
                    {TEXT_LEFT}
                </span>
            </span>
        )
    }

    renderRecord = (data) => {
        if (!Array.isArray(data)) {
            return null;
        }
        return data.map((item, index) => {
            return <Collapse key={`coll-${item.year}${item.mobth}-${index}`}
                year={item.year}
                month={item.month}
                fee={item.fee}
                item={item.data} />
        });
    }

    render() {

        const { data, address, name } = this.props;

        return (
            <div className={styles.root}>
                {this.renderHeader()}
                {
                    address ? <div className={styles.houseInfo}>
                        <span className={styles.house}>
                            <img className={styles.img}
                                src={ICON_ROOM}
                                alt='' />
                            <span className={styles.address}>
                                {address}
                            </span>
                        </span>
                        <span className={styles.houseName}>
                            {name}
                        </span>
                    </div> : null
                }
                {this.renderRecord(data)}
                <div className={styles.payContainer}>
                    <Button rootClass={styles.toPay}
                        label={BTN_TO_PAY}
                        onClick={this.onToPay} />
                </div>
            </div>
        )
    }
}

export default withRouter(FeeRecord);


