import * as React from 'react';
import Button from '../../common/components/Button';
import Modal from '../../common/components/Modal';
import styles from './index.scss';

const ICON_PHONE_COLOR = require('../../common/resources/phone_color.png');
const ICON_ROOM_LIST = require('../../common/resources/room_list.png');
const ICON_SELECTED = require('../../common/resources/selectd.png');
const ICON_NORMAL = require('../../common/resources/radio.png');
const ICON_DELETE = require('../../common/resources/delete.png');
const BTN_ADDING = '';

class HouseList extends React.Component {

    static defaultProps = {
        phone: '18516501895',
        data: [
            {
                address: '',
                isDefault: true
            },
            {
                address: 'A',
                isDefault: false
            },
            {
                address: 'BBBBBBB',
                isDefault: false
            },
            {
                address: 'C',
                isDefault: false
            }
        ]
    }


    requestSetDefault = () => {
        return Promise.resolve();
    }

    onSetDefault = (item) => {
        this.modal.show({
            content: '',
            onConfirm: this.requestSetDefault
        })
    }

    requestDelete = () => {
        return Promise.resolve();
    }

    onDelete = (item) => {
        this.modal.show({
            content: '',
            onConfirm: this.requestDelete
        });
    }

    onToAdd = () => {
        const { history } = this.props;
        history.push('/adding');
    }

    renderRoomList = (data) => {

        if (!Array.isArray(data)) {
            return null;
        }
        return (
            <div className={styles.infoContainer}>
                <div className={styles.header}>
                    <img className={styles.icon}
                        src={ICON_ROOM_LIST}
                        alt='' />
                    <span className={styles.title}>
                        {
                            ''
                        }
                    </span>
                </div>
                {
                    data.map((item, index) => {
                        const ICON = item.isDefault ? ICON_SELECTED : ICON_NORMAL;
                        const noBorder = index === (data.length - 1) ? styles.noBorder : '';
                        return (
                            <div key={`room-${item.address}-${index}`}
                                className={`${styles.content} ${noBorder}`}>
                                <span className={styles.address}>
                                    {item.address}
                                </span>
                                <span className={styles.operation}>
                                    <img className={styles.normal}
                                        onClick={() => {
                                            this.onSetDefault(item)
                                        }}
                                        src={ICON}
                                        alt='' />
                                    <img className={styles.normal}
                                        onClick={() => {
                                            this.onDelete(item)
                                        }}
                                        src={ICON_DELETE}
                                        alt='' />
                                </span>
                            </div>
                        )
                    })
                }
            </div>
        )
    }

    render() {

        const { phone, data } = this.props;

        return (
            <div className={styles.root}>
                <div className={styles.phoneContainer}>
                    <img className={styles.img}
                        src={ICON_PHONE_COLOR}
                        alt='' />
                    <div className={styles.labelContainer}>
                        <span className={styles.label}>
                            {""}
                        </span>
                        <span className={styles.phone}>
                            {phone}
                        </span>
                    </div>

                </div>
                {
                    this.renderRoomList(data)
                }
                <Button rootClass={styles.adding}
                    label={BTN_ADDING}
                    onClick={this.onToAdd} />
                <Modal ref={(modal) => this.modal = modal} />
            </div>
        )
    }
}

export default HouseList;


