import * as React from 'react';
import styles from './index.scss';

const TEXT_LEFT = '';

class Header extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isLeft: true
        }
    }

    onChangeRecord = () => {
        const { onChangeView } = this.props;
        this.setState({
            isLeft: !this.state.isLeft
        }, () => {
            if ('function' === typeof onChangeView) {
                onChangeView(this.state.isLeft)
            }
        });
    }

    render() {

        const { isLeft } = this.state;
        const { isHideSwitch } = this.props;
        const _lStyle = { display: isLeft ? 'inline-block' : 'none' }
        const _rStyle = { display: isLeft ? 'none' : 'inline-block' }
        return (
            <div className={styles.root}>
                <span className={styles.left}>
                    <span className={styles.symbol}>
                    </span>
                    <span className={styles.label}>
                        {TEXT_LEFT}
                    </span>
                </span>
                {
                    isHideSwitch ? null : <span className={styles.right}
                        onClick={this.onChangeRecord}>
                        <span className={styles.container}>
                            <span className={styles.circle}
                                style={_lStyle}></span>
                            <span className={`${styles.circle} ${styles.active}`}
                                style={_rStyle}></span>
                        </span>
                    </span>
                }
            </div>
        )
    }
}

export default Header;


