import * as React from 'react';
import styles from './index.scss';

const TEXT_PAYMENT_TYPE = '';
const TEXT_NO_RECORD = '';
const ICON_NO_RECORD = require('../../../../common/resources/no_record.png');
const ICON_DOWN_BLACK = require('../../../../common/resources/down_black.png');

class List extends React.Component {
    static defaultProps = {
        isCollapse: false
    };

    constructor(props) {
        super(props);
        this.state = {
            isCollapse: this.props.isCollapse
        }
    }

    onCollapse = () => {
        this.setState({
            isCollapse: !this.state.isCollapse
        });
    }

    onToFeeDetail = (data, year) => {
        const {history} = this.props;
        history.push(`/detail/${year}/${data.month}`);
    }

    renderList = (obj) => {
        let elem = null;
        if ('object' === typeof obj) {
            elem = (
                <React.Fragment>
                    <div className={styles.header}
                        onClick={this.onCollapse}>
                        <span className={styles.label}>
                            {obj.year}
                        </span>
                        <span className={styles.iconContainer}>
                            <img className={styles.icon}
                                src={ICON_DOWN_BLACK} alt='' />
                        </span>
                    </div>
                    {
                        !!obj.data.length ? obj.data.map((item) => {
                            return this.renderListItem(item, obj)
                        }) : this.renderDefault()
                    }
                </React.Fragment>
            );
        }

        return elem;
    }

    renderListItem = (data, obj) => {
        const { isCollapse } = this.state;
        const _style = { display: isCollapse ? "none" : "flex" };
        const { address, fee } = data;
        return (
            <div className={styles.listItem}
                key={`${obj.year}${data.month}`}
                onClick={() => { this.onToFeeDetail(data, obj.year) }}
                style={_style}>
                <span className={styles.left}>
                    <span className={styles.time}>
                        <span className={styles.month}>
                            {`${data.month}`}
                        </span>
                        <span className={styles.day}>
                            5
                       </span>
                    </span>
                    <span className={styles.infoContainer}>
                        <span className={styles.type}>
                            {TEXT_PAYMENT_TYPE}
                        </span>
                        <span className={styles.address}>
                            {address}
                        </span>
                    </span>
                </span>
                <span className={styles.fee}>
                    {fee}
                </span>
            </div>
        )
    }

    renderDefault = () => {
        const { isCollapse } = this.state;
        const _style = { display: isCollapse ? "none" : "flex" };
        return (
            <div className={styles.default}
                style={_style}>
                <span className={styles.container}>
                    <img className={styles.icon}
                        src={ICON_NO_RECORD} alt={''} />
                </span>
                <span className={styles.label}>
                    {TEXT_NO_RECORD}
                </span>
            </div>
        )
    }

    render() {
        const { data } = this.props;
        return (
            <div className={styles.root}>
                {this.renderList(data)}
            </div>
        )
    }
}

export default List;


