import * as React from 'react';

import Select from '../../common/components/Select';
import IconInput from '../../common/components/IconInput';
import Button from '../../common/components/Button';

import styles from './index.scss';

const PHONE_ICON = require('../../common/resources/phone.png');
const KEY_ICON = require('../../common/resources/key.png');

const BINDING = '';
const PREFIX_PROTOCOL = '';
const PROTOCOL = '';
const MockData = [
    {
        key: 0,
        label: ''
    },
    {
        key: 1,
        label: '1'
    },
    {
        key: 2,
        label: '2'
    }
]

class BindingHouse extends React.Component {

    componentWillMount() {
        console.log("WillMount==>", this.props);
    }

    render() {
        return (
            <div className={styles.root}>
                <div className={styles.logo}>
                </div>
                <div className={styles.form}>
                    <div className={styles.select}>
                        <div className={styles.item}>
                            <Select data={MockData}/>
                        </div>
                        <div className={styles.item2}>
                            <Select data={MockData}/>
                        </div>
                    </div>
                    <div className={styles.select}>
                        <div className={styles.item}>
                            <Select data={MockData}/>
                        </div>
                        <div className={styles.item2}>
                            <Select data={MockData}/>
                        </div>
                    </div>
                    <IconInput icon={PHONE_ICON} />
                    <IconInput icon={KEY_ICON} hasRightButton={true} />
                    <Button rootClass={styles.binding} label={BINDING} />
                    <div className={styles.container}>
                        {PREFIX_PROTOCOL}
                        <a className={styles.protocol}
                            href={"https://www.baidu.com"}>
                            {PROTOCOL}
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}

export default BindingHouse;


