import * as React from 'react';
import styles from './index.scss';

const TEXT_NO_RECORD = '';
const TEXT_NO_BILL = '';
const ICON_NO_RECORD = require('../../../../common/resources/no_record.png');

class Card extends React.Component {

    onToFeeDetail = (year, month) => {
        const { history } = this.props;
        history.push(`detail/${year}/${month}`)
    }

    renderCardView = (data, year) => {
        if (!Array.isArray(data)) {
            return this.renderCardDefault();
        }

        const _data = data.map((item) => {
            return item
        });

        while (_data.length < 12) {
            let item = {
                month: _data.length + 1
            };
            _data.push(item);
        }

        let list = [];
        let i = 0;
        while (i < _data.length) {
            if ((i + 3) >= _data.length) {
                list.push(this.renderCardLine(_data.slice(i, _data.length), i, year));
            }
            else {
                list.push(this.renderCardLine(_data.slice(i, i + 3), i, year));
            }
            i += 3;
        }

        return list;
    }

    renderCardLine = (data, key, year) => {
        if (!Array.isArray(data)) {
            return null;
        }

        let item = data.map((item, index) => {
            if (!!item.fee) {
                return this.renderCardItem(item, year);
            }
            return this.renderDefaultItem(item);
        });

        // if (item.length === 1) {
        //     item.push(<div key={`placeholder${item.length + 1}`}
        //         className={styles.placeHolder}></div>);
        //     item.push(<div key={`placeholder${item.length + 2}`}
        //         className={styles.placeHolder}></div>);
        // }
        // else if (2 === item.length) {
        //     item.push(<div key={`placeholder${item.length}`}
        //         className={styles.placeHolder}></div>);
        // }

        return (
            <div className={styles.line} key={`line-${key}`}>
                {
                    item
                }
            </div>
        )
    }

    renderCardItem = (data, year) => {

        if ('object' !== typeof data) {
            return null;
        }

        let _class_bg = data.isPaid ? styles.infoBg : '';

        return (
            <div className={styles.cardItem}
                onClick={() => {
                    this.onToFeeDetail(year, data.month)
                }}
                key={`${data.month}-${data.fee}`}>
                <div className={styles.month}>{`${data.month}`}</div>
                <div className={`${styles.info} ${_class_bg}`}>
                    <div className={styles.fee}>{`${data.fee}`}</div>
                </div>
            </div>
        )
    }

    renderDefaultItem = (data) => {
        return (
            <div className={`${styles.cardItem} ${styles.itemDefault}`}
                key={`default-${data.month}`}>
                <div className={styles.month}>
                    {`${data.month}`}
                </div>
                <div className={`${styles.info} ${styles.itemDefault}`}>
                    <div className={styles.feeDefault}>{`${TEXT_NO_BILL}`}</div>
                </div>
            </div>
        )
    }

    renderCardDefault = () => {
        return (
            <div className={styles.default}>
                <span className={styles.container}>
                    <img className={styles.icon}
                        src={ICON_NO_RECORD} alt={''} />
                </span>
                <span className={styles.label}>
                    {TEXT_NO_RECORD}
                </span>
            </div>
        )
    }

    render() {
        const { data } = this.props;

        if ('object' !== typeof data) {
            return null;
        }

        return (
            <div className={styles.root}>
                <div className={styles.header}>
                    {
                        data.year
                    }
                </div>
                <div className={styles.content}>
                    {
                        Array.isArray(data.data) && !!data.data.length ? this.renderCardView(data.data, data.year) : this.renderCardDefault()
                    }
                </div>
            </div>
        )
    }
}

export default Card;


