import * as React from 'react';
import Collapse from '../../../../common/components/Collapse';
import styles from './index.scss';

const ICON_NO_FEE = require('../../../../common/resources/no_fee.png');
const TITLE_LIST = ["", ""];
const DATA_LIST = ["", ""]
const TEXT_NO_FEE = '';

/***
 * 
 */

class Tab extends React.Component {
    static defaultProps = {
        titleList: TITLE_LIST,
        dataList: DATA_LIST,
        hasFooter: false,
        rootClass: '',
        totalFee: '0.00',
        onIsShowFooter: null,
        onComputeTotalFee: null
    }
    constructor(props) {
        super(props);
        const { dataList } = this.props;
        if (Array.isArray(dataList) && dataList.length) {
            this.state = {
                index: 0,
                data: dataList[0],
            }
        }
        else {
            this.state = {
                index: 0,
                data: this.renderDefault(),
            }
        }
    }

    onCollapseSelect = (isSelected, id) => {
        const { data } = this.state;
        if (Array.isArray(data)) {
            data.forEach((item) => {
                if (+(item.year + item.month) > +id) {
                    item.isSelected = false;
                }
                else if (+(item.year + item.month) < +id) {
                    item.isSelected = true;
                }
                else {
                    item.isSelected = !isSelected;
                }
            });

            this.setState({
                data
            }, () => {
                const { onComputeTotalFee } = this.props;
                if (typeof onComputeTotalFee === 'function') {
                    onComputeTotalFee(data)
                }
            });
        }

    }

    onSelect = (index) => {
        const data = this.props.dataList[index] || [];
        const { onIsShowFooter } = this.props;
        if (data.length) {
            this.setState({
                index,
                data
            });
        }
        else {
            this.setState({
                index,
                data: this.renderDefault()
            });
        }

        if (typeof onIsShowFooter === 'function') {
            onIsShowFooter(!!data.length);
        }
    }

    renderTitle = (titleList) => {
        if (Array.isArray(titleList)) {
            return titleList.map((item, index) => {
                return (
                    <span key={`${item}${index}`}
                        className={`${styles.title} ${index === this.state.index ? styles.active : ''}`}
                        onClick={() => {
                            this.onSelect(index);
                        }}>
                        {item}
                    </span>
                )
            })
        }

        return null;
    }

    renderData = (data) => {
        if (Array.isArray(data)) {
            return data.map((item, index) => {
                return <Collapse key={`${item.hasLeftIcon}${item.isSelected}${item.fee}${index}`}
                    onSelect={this.onCollapseSelect}
                    item={item.item}
                    hasLeftIcon={item.hasLeftIcon}
                    isSelected={item.isSelected}
                    year={item.year}
                    month={item.month}
                    fee={item.fee} />
            });
        }

        return data;
    }

    renderDefault = () => {
        return (
            <div className={styles.default}>
                <span className={styles.container}>
                    <img className={styles.icon}
                        src={ICON_NO_FEE} alt={''} />
                </span>
                <span className={styles.label}>
                    {TEXT_NO_FEE}
                </span>
            </div>
        )
    }

    render() {
        const { data } = this.state;
        const { titleList,
            dataList,
            rootClass } = this.props;
        return (
            <div className={`${styles.root} ${rootClass}`}>
                <div className={styles.titleBar}>
                    {this.renderTitle(titleList)}
                </div>
                <div className={styles.content}>
                    {

                        !!dataList.length ? this.renderData(data) : this.renderDefault()
                    }
                    {/* {
                        Array.isArray(data) && data.length ? <div className={styles.footer}>
                            <Radio rootClass={styles.radio}
                                data={DATA_SELECT} />
                            <span>
                                {`${data.length}`}
                            </span>
                            <span>
                                {``}
                                <span>
                                    {`${totalFee}`}
                                </span>
                            </span>
                        </div> : null
                    } */}
                </div>
            </div>
        )
    }

}

export default Tab;


