import React from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import {
    HashRouter as Router,
    Route,
    Switch
} from 'react-router-dom';
import { Provider } from 'react-redux';
import { store } from './store';

import MyApp from './pages/App/App';
import Signup from './pages/Singup';
import BindingHouse from './pages/BindingHouse';
import PaymentResult from './pages/PaymentResult';
import Payment from './pages/Payment';
import HouseList from './pages/HouseList';
import AddingHouse from './pages/AddingHouse';
import HouseFee from './pages/HouseFee';
import PaymentRecord from './pages/PaymentRecord';
import FeeDetail from './pages/FeeDetail';

import styles from './index.scss';

import registerServiceWorker from './registerServiceWorker';

const PATH_TO_TITLE = new Map([
    ['/', 'app'],
    ['/signup', ''],
    ['/home', 'home'],
    ['/other', 'other']
]);

const RETURN_LABEL = '';

class App extends React.Component {
    static contextTypes = {
        router: PropTypes.object
    };
    constructor(props) {
        super(props);
        this.state = {
            title: 'app'
        }
    }
    componentDidMount() {
        const {
            history
        } = this.context.router;
        this.unlisten = history.listen((location, action) => {
            this.setState({
                title: PATH_TO_TITLE.get(location.pathname) || ''
            })
        });
    }
    componentWillUnmount() {
        if (this.unlisten) {
            // 
            this.unlisten();
        }
    }
    render() {
        return (
            <div className={styles.root}>
                <div className={styles.header}>
                    <span className={styles.container}>
                        <span className={styles.arrow}>

                        </span>
                        <span className={styles.label}>
                            {RETURN_LABEL}
                        </span>
                    </span>
                    <span className={styles.title}>
                        {
                            this.state.title
                        }
                    </span>
                    <span className={styles.menu}>
                        
                    </span>
                </div>
                <div className={styles.content}>
                    {
                        this.props.children
                    }
                </div>
            </div>
        )
    }
}

let elem = (
    <Provider store={store}>
        < Router >
            {/* <App> */}
            <Switch >
                <Route path='/' exact component={MyApp}></Route>
                <Route path='/signup' exact component={Signup}></Route>
                <Route path='/binding' exact component={BindingHouse}></Route>
                <Route path='/payment' exact component={Payment}></Route>
                <Route path='/result' exact component={PaymentResult}></Route>
                <Route path='/houselist' exact component={HouseList}></Route>
                <Route path='/adding' exact component={AddingHouse}></Route>
                <Route path='/fee' exact component={HouseFee}></Route>
                <Route path='/detail/:year/:month' exact component={FeeDetail}></Route>
                <Route path='/record' exact component={PaymentRecord}></Route>
                <Route render={
                    () => {
                        return <div > nothning </div>
                    }}></Route>
            </Switch>
            {/* </App> */}
        </Router>
    </Provider>
)

ReactDOM.render(elem, document.getElementById('app'));
registerServiceWorker();


