import * as React from 'react';

import IconSelect from '../../common/components/IconSelect';
import Button from '../../common/components/Button';

import styles from './index.scss';

const HOUSING_ICON = require('../../common/resources/housing.png');
const ZONE_ICON = require('../../common/resources/zone.png');
const BUILDING_ICON = require('../../common/resources/building.png');
const ROOM_ICON = require('../../common/resources/root.png');
const BTN_LABEL = '';

const mockData1 = [
    {
        key: 0,
        label: ''
    },
    {
        key: 1,
        label: ''
    }
];
const mockData2 = [
    {
        key: 0,
        label: ''
    },
    {
        key: 1,
        label: ''
    }
];

const mockData3 = [
    {
        key: 0,
        label: ''
    },
    {
        key: 1,
        label: '1'
    }
];

const mockData4 = [
    {
        key: 0,
        label: ''
    },
    {
        key: 1,
        label: '101'
    }
];




class AddingHouse extends React.Component {

    onConfirm = () => {

    }
    render() {
        const { housingData = mockData1,
            zoneData = mockData2,
            buildingData = mockData3,
            roomData = mockData4 } = this.props;
        return (
            <div className={styles.root}>
                <div className={styles.iconSelect}>
                    <IconSelect icon={HOUSING_ICON}
                        data={housingData} />
                </div>
                <div className={styles.iconSelect}>
                    <IconSelect icon={ZONE_ICON}
                        data={zoneData} />
                </div>
                <div className={styles.iconSelect}>
                    <IconSelect icon={BUILDING_ICON}
                        data={buildingData} />
                </div>
                <div className={styles.iconSelect}>
                    <IconSelect icon={ROOM_ICON}
                        data={roomData} />
                </div>
                <Button rootClass={styles.btn}
                    label={BTN_LABEL}
                    onClick={this.onConfirm} />
            </div>
        )
    }
}

export default AddingHouse;


