import * as React from 'react';
import styles from './index.scss';

const NORMAL_ICON = require('../../resources/circle.png');
const SELECTED_ICON = require('../../resources/selectd.png');

class Radio extends React.Component {
    getData = () => {
        const { isSelected } = this.state;
        return isSelected ? this.props.data : null;
    }

    isSelected = () => {
        return this.state.isSelected;
    }

    constructor(props) {
        super(props);
        this.state = {
            isSelected: false
        }
    }

    onClick = () => {
        this.setState({
            isSelected: !this.state.isSelected
        });
    }

    render() {
        const { data = {}, rootClass = '' } = this.props;
        const { isSelected } = this.state;
        const target = isSelected ? SELECTED_ICON : NORMAL_ICON;
        return (
            <div className={`${styles.root} ${rootClass}`}>
                <img className={styles.icon}
                    onClick={this.onClick}
                    src={target}
                    alt='' />
                <span className={styles.label}>
                    {data.label}
                </span>
            </div>
        )
    }
}

export default Radio;


