import * as React from 'react';
import styles from './index.scss';
import Select from '../../common/components/Select';
import Tab from './components/Tab';
import Header from './components/Header';
import Footer from './components/Footer';

const mockData = [
    [
        {
            hasLeftIcon: true,
            isSelected: false,
            year: '2018',
            month: '12',
            fee: '120.00',
            item: {
                manager: '100.00',
                sanitation: '10.00',
                elevator: '10.00'
            }
        },
        {
            hasLeftIcon: true,
            isSelected: true,
            year: '2018',
            month: '11',
            fee: '120.00',
            item: {
                manager: '100.00',
                sanitation: '10.00',
                elevator: '10.00'
            }
        },
        {
            hasLeftIcon: true,
            isSelected: true,
            year: '2018',
            month: '10',
            fee: '120.00',
            item: {
                manager: '100.00',
                sanitation: '10.00',
                elevator: '10.00'
            }
        },
        {
            hasLeftIcon: true,
            isSelected: true,
            year: '2018',
            month: '09',
            fee: '120.00',
            item: {
                manager: '100.00',
                sanitation: '10.00',
                elevator: '10.00'
            }
        }
    ],
    []
];

class HouseFee extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            totalFee: this.computeTotalFee(mockData[0]),
            isShowFooter: true
        }
    }


    onComputeTotalFee = (data) => {
        this.setState({
            totalFee: this.computeTotalFee(data)
        });
    }

    onIsShowFooter = (isShow) => {
        this.setState({
            isShowFooter: isShow
        })
    }

    render() {
        const { dataList = mockData } = this.props;
        const { totalFee, isShowFooter } = this.state;

        return (
            <div className={styles.root}>
                <Header url={'/record'} />
                <div className={styles.selectContainer}>
                    <Select rootClass={styles.select} />
                </div>
                <Tab dataList={dataList}
                    onComputeTotalFee={this.onComputeTotalFee}
                    onIsShowFooter={this.onIsShowFooter} />
                {
                    isShowFooter ? <Footer fee={totalFee} /> : null
                }
            </div>
        )
    }

    computeTotalFee = (data) => {
        let totalFee = 0;
        if (Array.isArray(data)) {
            data.forEach((item, index) => {
                if (item.isSelected) {
                    totalFee += +item.fee;
                }
            });

            return totalFee.toFixed(2);
        }

        return totalFee.toFixed(2);
    }
}

export default HouseFee;


