import { combineReducers } from 'redux';
import * as t from '../actions/actionTypes';

export const payment = (state = {}, action) => {
    switch (action.type) {
        case t.GET_LIST:
            return { status: 0 }
        case t.GET_LIST_FAIL:
            return { status: 2 };
        case t.GET_LIST_SUCCESS:
            return { status: 1 }
        default:
            return state;
    }
}


