import * as React from 'react';

import Button from '../Button';

import styles from './index.scss';

const GET_VERIFICATION_CODE = '';


class IconInput extends React.Component {
    static defaultProps = {
        icon: '',/**/
        maxLength: 30,
        hasRightButton: false
    }
    render() {

        const { icon = '', maxLength = 30, hasRightButton } = this.props;
        return (
            <div className={styles.root}>
                {
                    !!icon ? <span className={styles.icon}>
                        <img className={styles.img}
                            src={this.props.icon}
                            alt='' />
                    </span> : null
                }
                <div className={styles.container}>
                    <input className={styles.input} maxLength={maxLength} />
                    {
                        hasRightButton ? <Button rootClass={styles.right} label={GET_VERIFICATION_CODE}></Button> : null
                    }
                </div>
            </div>
        )
    }
}

export default IconInput;


