import * as React from 'react';
import styles from './index.scss';

const DROP_DOWN_ICON = require('../../resources/drop_down_w.png');

class Select extends React.Component {
    constructor(props) {
        super(props);
        if (Array.isArray(this.props.data) && typeof this.props.data[0] === 'object') {
            this.state = {
                key: 0,
                label: this.props.data[0].label,
                isShowMenu: false
            }
        }
        else {
            this.state = {
                key: undefined,
                label: undefined,
                isShowMenu: false
            }
        }
    }

    getData = () => {
        return {
            key: this.state.key,
            label: this.state.value
        }
    }

    onSelect = () => {
        this.setState({
            isShowMenu: !this.state.isShowMenu
        })
    }

    onSelectItem = (item) => {
        this.setState({
            key: item.key,
            label: item.label,
            isShowMenu: false
        });
    }

    renderList = (data) => {
        if (!Array.isArray(data)) {
            return null;
        }

        return data.map((value, index) => {
            return <div key={value.key}
                className={styles.item}
                onClick={() => {
                    this.onSelectItem(value);
                }}
            >
                {value.label}
            </div>
        });

    }
    render() {
        const { data, rootClass, inputClass } = this.props;
        const { label, isShowMenu } = this.state;
        const _style = { display: isShowMenu ? 'block' : 'none' };
        return (
            <div className={`${styles.root} ${rootClass}`}>
                <input className={`${styles.input} ${inputClass}`}
                    type="text"
                    readOnly
                    value={label}
                    onClick={this.onSelect} />
                <span className={styles.container}
                    onClick={this.onSelect}>
                    <img className={styles.icon}
                        src={DROP_DOWN_ICON}
                        alt={''} />
                </span>
                <div className={styles.menu}
                    style={_style}>
                    {this.renderList(data)}
                </div>
            </div>
        )
    }
}

export default Select;


