import * as React from 'react';
import * as ReactDOM from 'react-dom';
import Button from '../Button';
import styles from './index.scss';

const BTN_CANCEL = '';
const BTN_CONFIRM = '';

let container = null;

class Modal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isShow: false,
            content: '',
            header: undefined,
            footer: undefined,
            confirmLabel: undefined,
            onConfirm: null,
            onCancel: null,
            cancelLabel: undefined
        };
        if (null === container) {
            container = document.createElement('div');
        }
    }

    show = (options) => {
        this.setState({
            isShow: true,
            ...options
        });
    }

    hide = () => {
        this.setState({
            isShow: false
        });
    }

    onCancel = () => {
        const { onCancel } = this.state;
        if ('function' === typeof onCancel) {
            onCancel();
        }
        this.hide();
    }

    onConfirm = () => {
        const { onConfirm } = this.state;
        if ('function' === typeof onConfirm) {
            onConfirm();
        }
        this.hide();
    }

    componentDidMount() {
        document.body.appendChild(container);
    }

    componentWillUnmount() {
        if (null !== container) {
            container = null;
        }
    }

    renderFooter = (data) => {

    }

    renderDefaultFooter = () => {
        const { confirmLabel, cancelLabel } = this.state;
        return (
            <React.Fragment>
                <Button rootClass={styles.cancel}
                    onClick={this.onCancel}
                    label={cancelLabel || BTN_CANCEL} />
                <Button rootClass={styles.confirm}
                    onClick={this.onConfirm}
                    label={confirmLabel || BTN_CONFIRM} />
            </React.Fragment>
        )
    }

    render() {
        const { header, footer, content } = this.state;
        const { isShow } = this.state;
        const _style = {
            display: isShow ? 'flex' : 'none'
        };

        let elem = <div className={styles.mask}
            style={_style}>
            <div className={styles.root}>
                {
                    !!header ? <header className={styles.header}>
                        {header}
                    </header> : null
                }
                <div className={styles.content}>
                    {content}
                </div>
                <div className={styles.footer}>
                    {
                        Array.isArray(footer) && footer.length ?
                            this.renderFooter(footer) :
                            this.renderDefaultFooter()
                    }
                </div>
            </div>
        </div>
        return ReactDOM.createPortal(elem, container);
    }
}

export default Modal;


