import * as React from 'react';
import {withRouter} from 'react-router';
import styels from './index.scss';

const TEXT_LEFT = '';
const TEXT_RIGHT = '';

function Header(props) {

    function onToRecord() {
        const { url, history } = props;
        if (!history) {
            return;
        }
        history.push(url);
    }

    return (
        <div className={styels.root}>
            <span className={styels.left}>
                <span className={styels.symbol}>
                </span>
                <span className={styels.label}>
                    {TEXT_LEFT}
                </span>
            </span>
            <span className={styels.right}
                onClick={onToRecord}>
                {TEXT_RIGHT}
            </span>
        </div>
    )
}

export default withRouter(Header);


