export const get = (url) => {
    return fetch(url, {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Accept': 'application/json, text/plain, */*'
        }
    }).then((res) => {
        // res
        if (res.status >= 200 && res.status < 300) {
            return res.json();
        }
        else {
            // 404500
            const error = new Error('');
            error.res = res;
            return error
        }
    });
}

/*
 *  httppostContent-Type
 *  Content-Type:
 * 'application/x-www-form-urlencoded': encodeURIComponent key1=val1&key2=val2 
 * 'multipart/form-data':  <form>  enctype  multipart/form-data
 * 'application/json': JSON
 * 'text/xml'
 */
export const post = (url, params) => {
    return fetch(url, {
        method: 'POST',
        credentials: 'include',
        headers: {
            'Accept': 'Application/json, text/plain, */*',
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: objToJsonString(params)
    }).then((res) => {
        // res
        if (res.status >= 200 && res.status < 300) {
            return res.json();
        }
        else {
            // 404500
            const error = new Error('');
            error.res = res;
            return error
        }
    });
}

const objToJsonString = (obj) => {
    let result = '';
    let item;
    for (item in obj) {
        if ('object' === typeof obj[item]) {
            result += `&${item}=${encodeURIComponent(JSON.stringify(obj[item]))}`;
        }
        else {
            result += `&${item}=${obj[item]}`;
        }
    }

    if (!!result) {
        result = result.slice(1);
    }

    return result;
}



