import * as React from 'react';
import styles from './index.scss';

const ICON_UP = require('../../resources/up.png');
const ICON_DOWN = require('../../resources/down.png');

const ICON_NORMAL = require('../../resources/normal.png');
const ICON_SELECTED = require('../../resources/selectd.png');

/**
 * item: {
 * manager: '',
 * sanitation: '',
 * slevator: ''}
 */ 
const DATA_KEY_TO_MAP = {
    'manager': '',
    'sanitation': '',
    'elevator': ''
};

class Collapse extends React.Component {
    static defaultProps = {
        hasLeftIcon: false,
        year: '',
        month: '',
        fee: '',
        item: {},
        onSelect: null
    }

    getData = () => {
        if (this.state.isSelected && !!this.props.id) {
            return this.props.id;
        }
        
        return null;
    }

    constructor(props) {
        super(props);
        this.state = {
            isDown: true,
            isSelected: this.props.isSelected
        }
    }

    onSelect = () => {
        const {onSelect} = this.props;
        if (typeof onSelect === 'function') {
            const id = this.props.year + this.props.month;
            onSelect(this.state.isSelected, id);
        }
        else {
            this.setState({
                isSelected: !this.state.isSelected
            });
        }
    }

    onUpOrDown = () => {
        this.setState({
            isDown: !this.state.isDown
        })
    }

    renderItem = (obj) => {
        if (typeof obj !== 'object') {
            return null;
        }

        const keyList = Object.keys(obj);
        return keyList.map((item, index) => {
            return <div key={`${item}-${index}`}
                className={styles.item}>
                <span className={styles.itemLabel}>
                    {DATA_KEY_TO_MAP[item]}
                </span>
                <span className={styles.itemFee}>
                    <span className={styles.money}>
                        {`${obj[item]}`}
                    </span>
                </span>
            </div>
        })

    }

    render() {

        const { hasLeftIcon, year, month, fee, rootClass, item } = this.props;
        const { isDown, isSelected } = this.state;
        const _style = { display: isDown ? 'block' : 'none' };
        return (
            <div className={`${styles.root} ${rootClass}`}>
                <div className={styles.titleBar}>
                    <span className={styles.left}>
                        {
                            !!hasLeftIcon ? <span className={styles.leftIcon}
                                onClick={this.onSelect}>
                                <img className={styles.icon1}
                                    src={isSelected ? ICON_SELECTED : ICON_NORMAL}
                                    alt={''} />
                            </span> : null
                        }
                        <span className={styles.title}>
                            {`${year}${month}`}
                        </span>
                    </span>
                    <span className={styles.right}>
                        <span className={styles.fee}>
                            {`${fee}`}
                        </span>
                        <span className={styles.rightIcon}
                            onClick={this.onUpOrDown}>
                            <img className={styles.icon2}
                                src={isDown ? ICON_DOWN : ICON_UP}
                                alt={''} />
                        </span>
                    </span>
                </div>
                <div className={styles.content}
                    style={_style}>
                    {
                        this.renderItem(item)
                    }
                </div>
            </div>
        )
    }
}

export default Collapse;


